@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";


@keyframes enter-left {
    0% {

        left: -100vw;
    }
    100% {
        left: 0vw
        
    }
}

@keyframes exit-right {
    0% {
        left: 0vw;

    }
    100% {
        left: 100vw;
        
    }
}

@keyframes enter-right {
    0% {
        left: 100vw;
    }
    100% {
        
        left: 0vw;   
    }
}

@keyframes exit-left {
    0% {
        left: 0vw;

    }
    100% {
        left: -100vw;
        
    }
}

.background-carousel {

    width: 100%;
    z-index: 1;
    flex-wrap:nowrap;
    overflow:hidden;
    background-color:transparent;
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    align-items:center;
    position: relative;
    @include mobile-only {
        align-items:flex-start;
    }
    &--full-width {
        width:100vw;
        /*min-height: -moz-fill-available;*/
        overflow:hidden;
        position:relative;
    }

    &__image {
        position:absolute;
        object-fit: contain;
        width: 100vw;
        height: 75vh;
        margin: 0 auto;
        left: 0vw;
        filter: saturate(0.6);
        object-position:center;
        padding: 1vw;
        
        box-sizing:border-box;
        @include mobile-only {
            
        }

        &--hidden {
            display:none;
        }
        &--enter-left {

            animation: 200ms ease-in-out forwards enter-left;
        }
        &--exit-left {

            animation: 200ms ease-in forwards exit-left;
        }
        &--exit-right{

            animation: 200ms ease-in forwards exit-right;
        }
        &--enter-right{

            animation: 200ms ease-in-out forwards enter-right;
        }
        &--next{
            left: -100vw;
            animation: 200ms ease-in 4s forwards enter-left;
        }
    }

    &__button {
        z-index:99;
        /*width: 5vw;*/
        height:5vw;
        margin: 0 4vw 0 6vw;
        background: unset;
        border:unset;
        font-size: 4vw;
        color:rgb(248, 248, 248);
        background: rgb(14, 159, 255);
        border: 1px solid rgb(255, 255, 255);
        display:flex;
        flex-direction:row;
        align-items:center;
        justify-content:center;
        border-radius: 0.5vw;
        margin-left: 4vw;
        @include mobile-only {
            font-size: 7vw;
            height:8vw;
            width: 10%;
            margin: 0 0;
            position: relative;
            top:45%;
            
            color:white;

        }
    }
}