@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.front-page-content {
    background: rgb(0,213,244);
    background: linear-gradient(216deg, rgba(0, 211, 244, 0.274) 8%, rgba(228,255,169,0) 52%, rgba(2,153,181,0.24691883589373254) 93%);
    width: 100%;
    height: 100vh;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:flex-start;
    clip-path: polygon(100% 0, 100% 90%, 50% 100%, 0 90%, 0 0);

    &__image {
        width: auto;
        object-fit: contain;
        width: 100%;
        height:100vh;
        padding: 5%;
        box-sizing:border-box;
        /*background-color: #445d7b;*/
        background-color:white;
        background: linear-gradient(245deg, rgba(174, 238, 236, 0.527) 38%, rgba(20, 177, 255, 0.397) 100%);
        
    }
    &__title {
        position:fixed;
        bottom: 15vh;
        color: rgb(0, 0, 0);
        background-color: rgba(255, 255, 255, 0.856);
        font-family: "Josefin Sans";
        font-size: 2.5vw;
        display: flex;
        align-items:flex-start;
        height: 5%;
        text-align:center;
        padding: 1vw;
        border: 1vh solid black;
        display:flex;
        align-items:center;
        justify-content:center;
        @include mobile-only {
            font-size: 7vw;
        }
        
    }
    &__catchphrase {
        color: rgb(0, 0, 0);
        background-color: rgb(255, 255, 255);
        font-family: "Josefin Sans";
        font-size: 1.5vw;
        display: flex;
        align-items:flex-start;
        height: 5%;
        text-align:center;
        position:absolute;
        top:63vh;
        right: 39vw;
        padding: 1vw;
        border: 1vh solid black;
        display:flex;
        align-items:center;
        justify-content:center;
    }
}