@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.skill {
    width: 10vw;
    height: 10vw;
    font-family: "Josefin Sans";
    font-size: 1.5vw;
    background: url("/images/stripes-light.png");
    padding: 1vw;
    margin:1vw;
    border-radius: 1vw;
    display:flex;
    flex-direction:column;
    justify-content: space-evenly;
    box-shadow: 0.2vw 0.2vw 0.3vw rgba(0, 0, 0, 0.603);
    @include mobile-only {
        width: 35vw;
        height: 35vw;
        font-size: 4vw;
    }
    

    &__image {
        height: 7vw;
        margin-bottom: 0.5vw;
        object-fit:contain;
        @include mobile-only {
            height: 20vw;
        }
    }
}