@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss"; 
.projects {
    width: 100%;
    height: max-content;
    background-color: rgb(61, 169, 184);
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    justify-content:center;
    align-items:flex-start;
    background: url("/images/stripes-light.png");
    box-sizing: border-box;
    overflow: hidden;
    &__title {
        width: 100%;
        font-family: "Josefin Sans";
        font-size: 3vw;
        background:rgb(61, 169, 184);
        margin-bottom: 3vw;
        color: rgb(238, 238, 238);
        @include mobile-only {
            font-size: 7vw;
        }
    }
    &__project-container {
        width: 80vw;
        height: auto;
        display:flex;
        background-color: rgb(255, 255, 255);
        box-shadow: 0.2vw 0.2vw 0.4vw black;
        background-size:cover;
        background-repeat:no-repeat;
    }
}

