@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.project-summary {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
    box-sizing:border-box;
    justify-content:center;
    padding: 1vw 0;
    @include mobile-only {
        width:100%;
        position:relative;
        padding-top: 7vw;
    }

    &__button {
        width: 20%;
        height: 9vw;
        margin: 0vw;
        font-size:1.2vw;
        border:none;
        color: white;
        background: linear-gradient(90deg, #4b6cb7 0%, #182848 100%);
        border-radius: 0.4vw;

    }
    &__labels-container {

    }
    &__browse {
        width: 10%;
        background-color:rgba(0, 0, 0, 0.445);
        box-shadow: 0.2vw 0.2vw 0.3vw rgba(0, 0, 0, 0.527);
        color: white;
        display:flex;
        flex-direction:row;
        align-items:center;
        justify-content:space-around;
        font-size:1.0vw;
        line-height:1.0vw;
        margin: 0 1%;
        border-radius: 0.2vw;
        @include mobile-only {
            font-size: 4vw;
            width: 50%;
            padding: 3vw 0;
            height: auto;
            position: absolute;
            top: -3vw;
            margin: 0;
            &--right {
                right: 0vw;
            }
            &--left {
                left: 0vw;
            }
        }
    }
    &__tech-container {
        margin-top: 1vw;
        width: max-content;
        height: 4vw;
        display:flex;
        flex-direction:row;
        justify-content:center;
        align-items: center;
        @include mobile-only {
            width: 50%;
            height: 20vw;
        }
        &__tech {
            height: 3vw;
            background-color: white;
            margin: 1vw;
            border-radius: 1vw;
            box-shadow: 0.2vw 0.2vw 0.3vw rgba(0, 0, 0, 0.527);
            padding: 0.3vw;
            @include mobile-only {
                width: auto;
                height: 7vw;
            }
        }

    }
    &__links-container{
        width: 40%;
        height: 4vw;
        margin-top: 1vw;
        padding-left: 3vw;
        display:flex;
        flex-direction:row;
        justify-content:center;
        align-items: center;
        @include mobile-only {
            width: 40%;
            height: 20vw
        }
        &__link {
            height: 3vw;
            background-color: white;
            margin: 1vw;
            border-radius: 1vw;
            border: 1px solid rgb(2, 133, 209);
            padding: 0.3vw;
            color:white;
            background-color:rgb(0, 81, 148);
            font-size: 3vw;
            @include mobile-only {
                font-size: 7vw;
                height: max-content;
                width: 7vw;
            }
        }
        &__label {
            height: 3vw;
            font-size: 1.0vw;
            line-height: 3vw;
            color:rgb(0, 81, 148);
            @include mobile-only {
                font-size: 3vw;
                display:none;
            }
        }

    }
    &__title {
        display:flex;
        flex-direction:row;
        align-items:center;
        justify-content:center;
        font-size:1vw;
        width: 25%;
        text-align:center;
        background:rgba(255, 154, 22, 0.794);
        color:white;
        box-shadow: 0.2vw 0.2vw 0.3vw rgba(0, 0, 0, 0.527);
        height: max-content;
        padding: 0.5vw;
        box-sizing:border-box;
        @include mobile-only {
            font-size: 4vw;
            width: 100%;
            font-weight:bold;
        }
        
    }
    &__description {
        display:flex;
        flex-direction:row;
        align-items:center;
        justify-content:center;
        font-size:1vw;
        width: 50%;
        text-align:center;
        background: dodgerblue;
        color: white;
        box-shadow: 0.2vw 0.2vw 0.3vw rgba(0, 0, 0, 0.527);
        height: max-content;
        padding: 0.5vw;
        box-sizing:border-box;
        @include mobile-only {
            font-size: 4vw;
            width:100%;
        }
    }
}