@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.footer {
    width: 100%;
    clip-path: polygon(50% 0%, 100% 20%, 100% 100%, 0 100%, 0 20%);
    background: linear-gradient(245deg, rgba(194,240,255,1) 0%, rgba(215,237,255,1) 85%);
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    justify-content:space-around;
    font-family: "Bungee Hairline";
    font-size: 1.3vw;
    font-family: "Josefin Sans";
    padding: 2vw;
    box-sizing:border-box;
    &__credits {
        width: 50%;
        margin-top: 1vw;
        @include mobile-only {
            font-size: 3vw;
        }
    }
    &__icon-link {
        font-size: 4vw;
        color:white;
        width: 33%;
        text-align:center;
        margin-top: 3vw;
        margin: 2vw 0;
        @include mobile-only {
            font-size: 10vw;
            width: 100%;
        }
    }
}