@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.language-selector {
    
    &__icons {
        z-index:99;
        width:8vw;
        position: fixed;
        top: 0.5vw;
        right: -1vw;
        display:flex;
        flex-direction:row;
        justify-content: space-evenly;
        /*background:rgba(255, 255, 255, 0.637);*/
        /*box-shadow: -0.1vw 0.1vw 0.1vw rgba(0, 0, 0, 0.377);*/

        @include mobile-only {
            top:3vw;
            flex-direction: column;
            align-items: center;
            height: auto;
            width: auto;
            right: 0;
            padding: 0.1vh;
            border-radius: unset;
            box-shadow: -0.5vw 0.5vw 0.5vw rgba(0, 0, 0, 0.377);
        }
        & img {
            object-fit:cover;
            width: 4vh;
            height: 4vh;
            margin-right: 1vh;
            @include mobile-only {
                margin: 0.2vh;
                width: 4vh;
                height: 2.5vh;
            }
        }
    }

}