@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.who-am-i {
    color: rgb(0, 87, 114);
    text-decoration:  none;
    width: 100%;
    display:flex;
    flex-direction: row;
    flex-wrap:wrap;
    padding: 4vw;
    justify-content:space-around;
    box-sizing:border-box;
    align-items:center;
    & i {
        font-size:5vw;
        @include mobile-only {
            font-size: 10vw;
        }
    }
    &__item {
        width: 25vw;
        font-size: 1.2vw;
        margin: 5vw;
        @include mobile-only {
            font-size: 3vw;
        }

    }
    &__title {
        font-size: 3vw;
        margin: 3vw 0;
        
    }
    &__image {
        width: 30%;
        height: auto;
        object-fit:cover;
        border-radius: 15%;
        @include mobile-only {
            width: 100%
        }

    }
}