@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.what-i-do {
    width: 100%;
    font-size: 1.4vw;
    display:flex;
    flex-direction:row;
    justify-content: space-evenly;
    align-items:center;
    box-sizing:border-box;
    font-weight:900;
    padding: 3vw 3vw;
    @include mobile-only {
        flex-direction: column;
        font-size: 2vh;
    }
    & > * {
        display: inline-block;
        color: #ffffff;
        margin: 3% 3%;
        width:28%;
        padding: 0.3vw;
        /*display:flex;*/
        justify-content: center;
        align-items:center;
        font-family: "Poiret One";
        background: linear-gradient(90deg, #d10e4fad 0%, #daaf51a8 100%);
        padding: 1vw;
        box-shadow: 0.2vw 0.2vw 0.4vw grey;
        border-radius: 0.5vw;
        @include mobile-only {
            max-width: unset;
            width: 100%;
            margin-top: 1vh;
            border-radius: 1vh;
            padding: 1vw 10vw;
        }
    }
}