
@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.header {
    width: 100%;
    box-sizing:border-box;
    position: fixed;
    top: 0;
    left:0;
    background-color: rgba(253, 253, 253, 0.89);
    display:flex;
    flex-direction:row;
    align-items:center;
    justify-content: flex-start;
    z-index:98;
    height: max-content;
    height: 3vw;
    @include mobile-only {
        
        height: auto;
    }


    &__title {
        color: rgb(0, 0, 0);
        font-family: "Bungee Hairline";
        font-size: 3vw;
        padding-left: 5vw;
        font-weight:bold;
        padding: 0;
        @include mobile-only {
            font-size: 7vw;
        }
    }
}