@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.stack {
    width: 100%;
    background-color: rgb(61, 169, 184);
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    justify-content:center;
    align-items:center;
    background: linear-gradient(245deg, rgba(174, 238, 236, 0.527) 38%, rgba(20, 177, 255, 0.397) 100%);
    padding: 2vw;
    box-sizing: border-box;
    clip-path: polygon(100% 0, 100% 90%, 50% 100%, 0 90%, 0 0);
    padding-bottom: 12vw;
    @include mobile-only {

    }
    
    &__title {
        width: 100%;
        margin-left: 20%;
        margin-right: 20%;
        font-family: "Josefin Sans";
        font-size: 3vw;
        margin-bottom: 3vw;
        @include mobile-only {
            font-size: 7vw;
        }

    }
}